import React, { useState } from 'react'

import DialogFullscreen from '../../components/UI/DialogFullscreen'
import { useTranslation } from 'react-i18next'
import {
    Box,
    Card,
    CardActionArea,
    CardActions,
    CardContent,
    createStyles,
    makeStyles,
    Paper,
    Typography,
} from '@material-ui/core'
import { AddCircle } from '@material-ui/icons'
import addBlocksEmpty from './addBlocksEmpty'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        title: {
            fontSize: 13,
            margin: '30px 15px 10px',
            textTransform: 'uppercase',
            fontWeight: 600,
        },
    })
)
const AddBlockMM = props => {
    const { t } = useTranslation()
    const classes = useStyles()
    const { addBlockOpen, setAddBlockOpen } = props

    return (
        <DialogFullscreen
            open={addBlockOpen}
            close={() => setAddBlockOpen(false)}
            title="Add block"
        >
            <div style={{ background: '#f5f5f5', flex: 1 }}>
                <Typography
                    className={classes.title}
                    color="textSecondary"
                    gutterBottom
                >
                    Empty
                </Typography>
                <Paper
                    style={{
                        padding: '15px',
                        paddingBottom: '10px',
                    }}
                >
                    {addBlocksEmpty.map(folder => {
                        return (
                            <Card
                                style={{
                                    cursor: 'pointer',
                                    flexShrink: 0,
                                    // width: '100px',
                                    marginRight: '10px',
                                    position: 'relative',
                                    minHeight: '160px',
                                    display: 'inline-block',
                                }}
                            >
                                <CardActionArea
                                    style={{
                                        // height: '100%',
                                        display: 'flex',
                                        alignItems: 'center',
                                        justifyContent: 'center',
                                    }}
                                    onClick={() => {
                                        props.addBlockTemplate(
                                            props.selectedBox,
                                            folder
                                        )
                                        setAddBlockOpen(false)
                                    }}
                                >
                                    <CardContent
                                        style={{
                                            flex: 1,
                                            display: 'flex',
                                            alignItems: 'center',
                                            justifyContent: 'center',
                                        }}
                                    />
                                    <CardActions>{folder.name}</CardActions>
                                </CardActionArea>
                            </Card>
                        )
                    })}
                </Paper>
            </div>
        </DialogFullscreen>
    )
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        addBlockTemplate: (parentId, folder) =>
            dispatch(actions.addBlockTemplate(parentId, folder)),
    }
}

export default connect(
    null,
    mapDispatchToProps
)(AddBlockMM)
