import React, { useEffect, useState } from 'react'
import {
    Button,
    Checkbox,
    Dialog,
    Divider,
    IconButton,
    Menu,
    MenuItem,
    Paper,
    Slide,
} from '@material-ui/core'
import {
    Add,
    Brush,
    Close,
    Delete,
    Image,
    Lock,
    LockOpen,
    More,
    MoreVert,
    Send,
    Title,
    Videocam,
} from '@material-ui/icons'
import { useTranslation } from 'react-i18next'

import Goal from '../Goal/Goal'
import Reward from '../Reward/Reward'
import Activity from '../Activity/Activity'
import DialogFullscreen from '../../components/UI/DialogFullscreen'
import EditResource from '../../components/EditResource/EditResource'
import Profile from '../Profile/Profile'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import * as actions from '../../store/actions'
import FolderMM from './FolderMM'
import ProfileMM from './ProfileMM'
import GoalMM from './old/GoalMM'
import PickerPopper from './PickerPopper'

const TransitionComponent = props => <Slide direction="up" {...props} />

const ChooseWrapMM = props => {
    const { t } = useTranslation()
    const [startResourceVisible, setStartResourceVisible] = useState('')
    const [editorKey, setEditorKey] = useState('')
    const [editingStyle, setEditingStyle] = useState(false)
    const [editingText, setEditingText] = useState(false)
    const [editingTextModal, setEditingTextModal] = useState(false)
    const [textValue, setTextValue] = useState()
    const [mainMenuAnchorEl, setMainMenuAnchorEl] = useState(null)
    const [anchorEl, setAnchorEl] = useState(null)
    const {
        el,
        id,
        selectedBox,
        setChangeParentMode,
        changeParentMode,
        addEmptyToStructure,
        editorMode,
        account,
        saveValueMM,
        deleteBlock,
        setAddBlockOpen,
        templateMasterBoxId,
        templateMasterVar,
        scale,
        setSelectedBox,
    } = props
    const [openElement, setOpenElement] = useState(
        props.history.location.hash.length > 0 &&
            (id === props.history.location.hash.substring(1) ||
                (el.type === 'profile' &&
                    props.history.location.hash.substring(1)) === 'profile')
    )

    useEffect(() => {
        if (
            templateMasterVar &&
            templateMasterBoxId &&
            templateMasterBoxId === id
        ) {
            setSelectedBox(templateMasterBoxId)
            setEditorKey(templateMasterVar.name)
        } else {
            setEditorKey('')
        }
    }, [templateMasterBoxId, templateMasterVar])

    useEffect(() => {
        if (!templateMasterBoxId) setEditorKey('')
    }, [selectedBox])

    const addMenu = (
        <Menu
            anchorEl={anchorEl}
            open={Boolean(anchorEl)}
            onClose={() => setAnchorEl(null)}
        >
            <MenuItem
                onClick={() => {
                    setAddBlockOpen(true)
                    setAnchorEl(null)
                }}
            >
                {t('g.block')}
            </MenuItem>
            <MenuItem
                onClick={() => {
                    setStartResourceVisible('goal')
                    setAnchorEl(null)
                }}
            >
                {t('g.goal')}
            </MenuItem>
        </Menu>
    )

    const mainMenu = (
        <Menu
            anchorEl={mainMenuAnchorEl}
            open={Boolean(mainMenuAnchorEl)}
            onClose={() => setMainMenuAnchorEl(null)}
        >
            {el.type === 'folder' && (
                <>
                    <MenuItem
                        onClick={() => {
                            setEditorKey('imageValue')
                            setMainMenuAnchorEl(null)
                            saveValueMM(id, {
                                imageOff: false,
                            })
                        }}
                    >
                        <div
                            style={{
                                width: '100%',
                                display: 'flex',
                                alignItems: 'center',
                            }}
                        >
                            <div style={{ flex: 1 }}>{t('g.image')}</div>
                            {el.imageValue && (
                                <div>
                                    <Checkbox
                                        onClick={e => {
                                            e.stopPropagation()
                                        }}
                                        onChange={e =>
                                            saveValueMM(id, {
                                                imageOff: !e.target.checked,
                                            })
                                        }
                                        checked={!el.imageOff}
                                        size="small"
                                    />
                                </div>
                            )}
                        </div>
                    </MenuItem>
                    <MenuItem
                        onClick={() => {
                            setEditorKey('videoURL')
                            setMainMenuAnchorEl(null)
                            saveValueMM(id, {
                                videoOff: false,
                            })
                        }}
                    >
                        <div
                            style={{
                                width: '100%',
                                display: 'flex',
                                alignItems: 'center',
                            }}
                        >
                            <div style={{ flex: 1 }}>{t('g.video')}</div>
                            {el.videoURL && (
                                <div>
                                    <Checkbox
                                        onClick={e => {
                                            e.stopPropagation()
                                        }}
                                        onChange={e =>
                                            saveValueMM(id, {
                                                videoOff: !e.target.checked,
                                            })
                                        }
                                        checked={!el.videoOff}
                                        size="small"
                                    />
                                </div>
                            )}
                        </div>
                    </MenuItem>
                    <MenuItem
                        onClick={() => {
                            setEditingText(true)
                            setMainMenuAnchorEl(null)
                            saveValueMM(id, {
                                textOff: false,
                            })
                        }}
                    >
                        <div
                            style={{
                                width: '100%',
                                display: 'flex',
                                alignItems: 'center',
                            }}
                        >
                            <div style={{ flex: 1 }}>{t('g.text')}</div>
                            {el.textValue && (
                                <div>
                                    <Checkbox
                                        onClick={e => {
                                            e.stopPropagation()
                                        }}
                                        onChange={e =>
                                            saveValueMM(id, {
                                                textOff: !e.target.checked,
                                            })
                                        }
                                        checked={!el.textOff}
                                        size="small"
                                    />
                                </div>
                            )}
                        </div>
                    </MenuItem>
                </>
            )}
            <MenuItem
                onClick={() => {
                    setEditorKey('styleBlock')
                    setMainMenuAnchorEl(null)
                }}
            >
                {t('g.styleBlock')}
            </MenuItem>
            <MenuItem
                onClick={() => {
                    setEditorKey('styleLine')
                    setMainMenuAnchorEl(null)
                }}
            >
                {t('g.styleLine')}
            </MenuItem>
            <MenuItem
                onClick={() => {
                    setChangeParentMode(changeParentMode === id ? '' : id)
                    setMainMenuAnchorEl(null)
                }}
            >
                {t('g.changeParent')}
            </MenuItem>

            {el.type === 'folder' && (
                <MenuItem
                    onClick={() => {
                        deleteBlock(id)
                        setMainMenuAnchorEl(null)
                    }}
                >
                    {t('g.delete')}
                </MenuItem>
            )}
        </Menu>
    )

    const handleSaveText = () => {
        saveValueMM(id, { textValue })
        setEditingText(false)
        setEditingTextModal(false)
    }

    return (
        <>
            <PickerPopper
                editorKey={editorKey}
                setEditorKey={setEditorKey}
                el={el}
                id={id}
                saveValueMM={saveValueMM}
            />
            <div
                style={{
                    flex: 1,
                    display: 'flex',
                    overflow: 'hidden',
                    flexDirection: 'column',
                }}
            >
                <Paper
                    style={{
                        background: 'white',
                        textAlign: 'right',
                        position: 'absolute',
                        top: '-50px',
                        whiteSpace: 'nowrap',
                        alignItems: 'start',
                        transform: 'scale(' + 1 / scale + ')',
                        transformOrigin: 'bottom left',
                    }}
                    className="buttonsWrapMM buttonsMM"
                >
                    {el.type !== 'folder' &&
                        (!editorMode || selectedBox === id) &&
                        !editingText &&
                        !editingStyle && (
                            <Button
                                onClick={() => setOpenElement(true)}
                                color="primary"
                                variant="contained"
                            >
                                {t('g.open')}
                            </Button>
                        )}
                    {editorMode && selectedBox === id && !templateMasterBoxId && (
                        <>
                            {editingStyle ? (
                                <div>
                                    <Button
                                        onClick={() => setEditingStyle(false)}
                                        style={{ marginRight: '10px' }}
                                        color="primary"
                                    >
                                        {t('g.back')}
                                    </Button>
                                    <Button
                                        onClick={() => {
                                            setEditorKey('lineType')
                                        }}
                                        variant="contained"
                                        style={{ marginRight: '10px' }}
                                    >
                                        {t('g.lineType')}
                                    </Button>
                                    <Button
                                        onClick={() => {
                                            setEditorKey('lineColor')
                                        }}
                                        variant="contained"
                                        style={{ marginRight: '10px' }}
                                    >
                                        {t('g.lineColor')}
                                    </Button>
                                    <Button
                                        onClick={() => {
                                            setEditorKey('lineWidth')
                                        }}
                                        variant="contained"
                                        style={{ marginRight: '10px' }}
                                    >
                                        {t('g.lineWidth')}
                                    </Button>
                                    <Button
                                        onClick={() => {
                                            setEditorKey('bgColor')
                                        }}
                                        variant="contained"
                                        style={{ marginRight: '10px' }}
                                    >
                                        {t('g.background')}
                                    </Button>
                                    <Button
                                        onClick={() => {
                                            setEditorKey('radius')
                                        }}
                                        variant="contained"
                                        style={{ marginRight: '10px' }}
                                    >
                                        {t('g.corners')}
                                    </Button>
                                </div>
                            ) : editingText ? null : (
                                <>
                                    <IconButton
                                        onClick={e =>
                                            setAnchorEl(e.currentTarget)
                                        }
                                        color="primary"
                                    >
                                        <Add />
                                    </IconButton>
                                    {addMenu}
                                    <IconButton
                                        onClick={() =>
                                            saveValueMM(id, {
                                                locked: !el.locked,
                                            })
                                        }
                                    >
                                        {el.locked ? <Lock /> : <LockOpen />}
                                    </IconButton>
                                    <IconButton
                                        onClick={e =>
                                            setMainMenuAnchorEl(e.currentTarget)
                                        }
                                    >
                                        <MoreVert />
                                    </IconButton>
                                    {mainMenu}
                                </>
                            )}
                        </>
                    )}
                </Paper>
                <div style={{ flex: 1, overflow: 'hidden', display: 'flex' }}>
                    {el.type === 'folder' && (
                        <FolderMM
                            el={el}
                            id={id}
                            editingText={editingText}
                            setEditingText={setEditingText}
                            editingTextModal={editingTextModal}
                            setEditingTextModal={setEditingTextModal}
                            selectedBox={selectedBox}
                            saveValueMM={saveValueMM}
                            textValue={textValue}
                            setTextValue={setTextValue}
                            handleSaveText={handleSaveText}
                            editorMode={editorMode}
                            scale={scale}
                        />
                    )}
                    {el.type === 'profile' && <ProfileMM />}

                    {el.type === 'goal' && <GoalMM id={id} />}
                </div>
            </div>
            <DialogFullscreen
                open={openElement}
                close={() => setOpenElement(false)}
                title="Opened res"
            >
                {el.type === 'profile' ? (
                    <Profile setChosenResource={setOpenElement} />
                ) : el.type === 'reward' ? (
                    <Reward id={el.id} setChosenResource={setOpenElement} />
                ) : el.type === 'activity' ? (
                    <Activity id={el.id} setChosenResource={setOpenElement} />
                ) : el.type === 'goal' ? (
                    <Goal id={el.id} setChosenResource={setOpenElement} />
                ) : null}
            </DialogFullscreen>
            {account._id && editorMode && (
                <Dialog
                    fullScreen
                    open={Boolean(startResourceVisible)}
                    onClose={() => {
                        setStartResourceVisible('')
                    }}
                    TransitionComponent={TransitionComponent}
                    scroll="paper"
                >
                    <EditResource
                        account={account}
                        friendData={props.friendData}
                        cancel={() => setStartResourceVisible('')}
                        type={startResourceVisible}
                        addEmptyToStructure={addEmptyToStructure}
                        selectedBox={id}
                    />
                </Dialog>
            )}
        </>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
        profile: state.profile,
        templateMasterBoxId: state.templateMasterBoxId,
        templateMasterVar: state.templateMasterVar,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        addDragStep: () => dispatch(actions.addDragStep()),
        addEmptyToStructure: (id, selectedBox, type) =>
            dispatch(actions.addEmptyToStructure(id, selectedBox, type)),
        addBlock: (id, blockType) => dispatch(actions.addBlock(id, blockType)),
        saveValueMM: (id, value) => dispatch(actions.saveValueMM(id, value)),
        deleteBlock: id => dispatch(actions.deleteBlock(id)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(ChooseWrapMM)
)
