import React, { useEffect, useState } from 'react'

import { TransformComponent } from 'react-zoom-pan-pinch'
import ContainerMM from './ContainerMM'
import { connect } from 'react-redux'
import * as wsActions from '../../websocketActions'
import * as actions from '../../store/actions'
import 'tui-image-editor/dist/tui-image-editor.css'

import BottomMenu from '../../components/Layout/BottomMenu'
import {
    Box,
    Button,
    Divider,
    Fab,
    FormControlLabel,
    IconButton,
    makeStyles,
    Paper,
    Slider,
    Snackbar,
    Switch,
} from '@material-ui/core'
import {
    Add,
    ArrowBack,
    Edit,
    Fullscreen,
    MyLocation,
    Remove,
    Save,
} from '@material-ui/icons'

import Layout from '../../components/Layout/Layout'
import { withRouter, Prompt } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import { useBeforeunload } from 'react-beforeunload'
import { SketchPicker } from 'react-color'
import AddBlockMM from './AddBlockMM'
import TemplateMaster from './TemplateMaster'

const useStyles = makeStyles(theme => ({
    edit: {
        position: 'fixed',
        bottom: '12px',
        right: '12px',
        margin: 0,
    },

    save: {
        position: 'fixed',
        bottom: theme.spacing(11),
        right: '12px',
    },
}))

const MindMap = props => {
    const { t } = useTranslation()
    const classes = useStyles()
    const [selectedBox, setSelectedBox] = useState('')

    const [changeParentMode, setChangeParentMode] = useState('')
    const [editorMode, setEditorMode] = useState(false)
    const [addBlockOpen, setAddBlockOpen] = useState(false)
    const {
        saveStructure,
        account,
        profile,
        setEventIsFromDraggable,
        scale,
        structureNotSaved,
        resetTransform,
        templateFolderId,
        zoomIn,
        zoomOut,
    } = props
    const isOwner =
        account &&
        account._id &&
        profile &&
        profile._id &&
        account._id === profile._id
    useBeforeunload(() => structureNotSaved && "You'll lose your data!")

    useEffect(() => {
        document.body.style.overflow = 'hidden'
        return () => {
            document.body.style.overflow = 'auto'
        }
    }, [])

    useEffect(() => {
        setEditorMode(false)
    }, [isOwner])

    // const [value, setValue] = useState(scale)

    // useEffect(() => {
    //     if (scale !== value) setScale(value)
    // }, [value])

    // useEffect(() => {
    //     if (scale !== value) setValue(scale)
    // }, [scale])

    const handleMouseDown = e => {
        if (editorMode) {
            const path = e.nativeEvent.composedPath()
            for (let el of path) {
                if (el) {
                    if (
                        el.getAttribute('id') === 'panWrap' ||
                        el === document.body
                    ) {
                        setEventIsFromDraggable(false)
                        return
                    }
                    const classN = el.getAttribute('class')
                    if (
                        classN &&
                        (classN.indexOf('react-draggable') > -1 ||
                            classN.indexOf('tinyEditorText_') > -1 ||
                            classN.indexOf('demo-toolbar-absolute') > -1 ||
                            classN.indexOf('noPan') > -1)
                    ) {
                        setEventIsFromDraggable(true)
                        return
                    }
                }
            }
        }
        setEventIsFromDraggable(false)
    }

    const handleClick = e => {
        // if (!changeParentMode) {
        //     if (editorMode) {
        //         const path = e.nativeEvent.composedPath()
        //         for (let el of path) {
        //             if (el) {
        //                 if (
        //                     el.getAttribute('id') === 'panWrap' ||
        //                     el === document.body
        //                 ) {
        //                     // setChosenResource('')
        //                     setSelectedBox('')
        //                     return
        //                 }
        //                 const classN = el.getAttribute('class')
        //                 if (
        //                     classN &&
        //                     (classN.indexOf('react-draggable') > -1 ||
        //                         classN.indexOf('tinyEditorText_') > -1)
        //                 ) {
        //                     return
        //                 }
        //             }
        //         }
        //     }
        //     // setChosenResource('')
        //     setSelectedBox('')
        // }
    }

    return (
        <>
            <Prompt
                when={structureNotSaved}
                message="You have unsaved changes, are you sure you want to leave?"
            />
            <div
                style={{
                    display: 'flex',
                    width: '100%',
                    height: '100%',
                    flexDirection: 'column',
                }}
            >
                <div
                    style={{
                        width: '100%',
                        background: 'white',
                        zIndex: 1,
                        // display: 'flex',
                    }}
                >
                    {isOwner ? (
                        <>
                            {templateFolderId ? (
                                <Layout
                                    noContent
                                    customHeader={
                                        <TemplateMaster
                                            setSelectedBox={setSelectedBox}
                                        />
                                    }
                                    color="#dcffe0"
                                />
                            ) : (
                                <div id="anchorTR" />
                            )}
                            <div
                                style={{
                                    position: 'fixed',
                                    left: '12px',
                                    bottom: '12px',
                                    display: 'flex',
                                    flexDirection: 'column',
                                    alignItems: 'center',
                                }}
                            >
                                <Paper
                                    elevation={3}
                                    style={{ marginBottom: '3px' }}
                                >
                                    <IconButton
                                        onClick={resetTransform}
                                        size="small"
                                    >
                                        <MyLocation />
                                    </IconButton>
                                </Paper>
                                <Paper elevation={3}>
                                    <IconButton onClick={zoomIn} size="small">
                                        <Add />
                                    </IconButton>
                                    <Divider />
                                    <IconButton onClick={zoomOut} size="small">
                                        <Remove />
                                    </IconButton>
                                </Paper>
                            </div>
                            <Paper elevation={3} className={classes.edit}>
                                <FormControlLabel
                                    style={{ margin: 0 }}
                                    control={
                                        <Switch
                                            checked={editorMode}
                                            onChange={event => {
                                                setEditorMode(
                                                    event.target.checked
                                                )
                                                setSelectedBox('')
                                            }}
                                            color="primary"
                                        />
                                    }
                                    label={t('g.edit')}
                                    labelPlacement="bottom"
                                />
                            </Paper>
                            {/* <Paper className={classes.edit} elevation={3}>
                                <IconButton
                                    color={editorMode ? 'primary' : 'default'}
                                    onClick={() => {
                                        setEditorMode(!editorMode)
                                        setSelectedBox('')
                                    }}
                                    size="medium"
                                >
                                    <Edit fontSize="large" />
                                </IconButton>
                            </Paper> */}
                            {isOwner && editorMode && structureNotSaved && (
                                <Fab
                                    onClick={saveStructure}
                                    color="primary"
                                    className={classes.save}
                                >
                                    <Save />
                                </Fab>
                            )}
                        </>
                    ) : (
                        <Layout noContent />
                    )}
                </div>

                <div
                    style={{
                        overflow: 'hidden',
                        flex: 1,
                        display: 'flex',
                    }}
                    id="panWrap"
                    onMouseDown={handleMouseDown}
                    onTouchStart={handleMouseDown}
                    onTouchEnd={() => setEventIsFromDraggable(false)}
                    onMouseUp={() => setEventIsFromDraggable(false)}
                    onClick={handleClick}
                >
                    <TransformComponent>
                        <div
                            style={{
                                cursor: 'grabbing',
                            }}
                        >
                            <ContainerMM
                                id="b0"
                                setSelectedBox={setSelectedBox}
                                selectedBox={selectedBox}
                                scale={scale}
                                editorMode={editorMode}
                                changeParentMode={changeParentMode}
                                setChangeParentMode={setChangeParentMode}
                                setAddBlockOpen={setAddBlockOpen}
                            />
                        </div>
                    </TransformComponent>
                </div>
            </div>

            <BottomMenu />
            <Snackbar
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'left',
                }}
                open={Boolean(changeParentMode)}
                message={t('g.chooseParent')}
                action={
                    <React.Fragment>
                        <Button
                            color="secondary"
                            size="small"
                            onClick={() => setChangeParentMode('')}
                        >
                            {t('g.UNDO')}
                        </Button>
                    </React.Fragment>
                }
            />
            <AddBlockMM
                addBlockOpen={addBlockOpen}
                setAddBlockOpen={setAddBlockOpen}
                selectedBox={selectedBox}
            />
        </>
    )
}
const mapStateToProps = state => {
    return {
        account: state.account,
        profile: state.profile,
        structureNotSaved: state.structureNotSaved,
        scale: state.scaleMM,
        templateFolderId: state.templateFolderId,
    }
}
const mapDispatchToProps = (dispatch: any) => {
    return {
        saveStructure: () => dispatch(actions.saveStructure()),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(MindMap)
)
