import React from 'react'
// import { Editor } from '@tinymce/tinymce-react'
import { Editor } from 'react-draft-wysiwyg'
import { convertFromRaw, ContentState, convertToRaw } from 'draft-js'
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css'
import htmlToDraft from 'html-to-draftjs'
import { useTranslation } from 'react-i18next'
import { IconButton } from '@material-ui/core'
import { Close } from '@material-ui/icons'

export default props => {
    const { t } = useTranslation()
    let contentState
    let initialValue = props.value

    if (!initialValue && props.MM)
        initialValue = '<p style="font-size: 36px;">Add text here</p>'

    if (typeof initialValue === 'string') {
        const blocksFromHtml = htmlToDraft(initialValue)
        const { contentBlocks, entityMap } = blocksFromHtml
        contentState = ContentState.createFromBlockArray(
            contentBlocks,
            entityMap
        )
        contentState = convertToRaw(contentState)
    } else contentState = initialValue
    return (
        // <Editor
        //     apiKey="cgz3v5s8p6m4ivfow51e7pin01evzej1lqy90qmi5z75ly57"
        //     // initialValue={form.getFieldValue('description')}
        //     init={{
        //         inline: true,
        //     }}
        //     onEditorChange={props.onEditorChange}
        //     value={props.value}
        // />
        <div
            className="buttonsMM"
            style={
                props.MM && !props.readOnly
                    ? {
                          background: 'rgba(255, 255, 255, .4)',
                          position: 'absolute',
                          width: '100%',
                      }
                    : {}
            }
        >
            <Editor
                placeholder={props.readOnly ? '' : t('g.addSomeText')}
                readOnly={props.readOnly}
                toolbarClassName={props.MM && 'demo-toolbar-absolute buttonsMM'}
                // toolbarOnFocus={props.MM}
                wrapperClassName={!props.MM && 'demo-wrapper'}
                {...(!props.readOnly ? {} : { contentState: contentState })}
                defaultContentState={contentState}
                onContentStateChange={value => {
                    props.onEditorChange && props.onEditorChange(value)
                    props.onEditorChangePlain &&
                        props.onEditorChangePlain(
                            convertFromRaw(value).getPlainText(' ')
                        )
                }}
                toolbarStyle={
                    props.MM
                        ? {
                              transform: 'scale(' + 1 / props.scale + ')',
                              transformOrigin: 'bottom left',
                          }
                        : {}
                }
                toolbar={{
                    options: [
                        'inline',
                        ...(props.MM ? [] : ['blockType']),
                        'fontSize',
                        'fontFamily',
                        'list',
                        'textAlign',
                        'colorPicker',
                        'link',
                        'emoji',
                        'remove',
                        'history',
                    ],
                    inline: { inDropdown: true },
                    list: { inDropdown: true },
                    textAlign: { inDropdown: true },
                    link: { inDropdown: true },
                    history: { inDropdown: true },
                }}
                toolbarHidden={props.readOnly}
                toolbarCustomButtons={
                    props.MM
                        ? [
                              <IconButton
                                  style={{
                                      position: 'absolute',
                                      right: '-40px',
                                      top: '-40px',
                                  }}
                                  color="primary"
                                  onClick={() => props.handleSaveText()}
                              >
                                  <Close />
                              </IconButton>,
                          ]
                        : []
                }
            />
        </div>
    )
}
