import React, { useState } from 'react'
import {
    Accordion,
    AccordionDetails,
    AccordionSummary,
    Box,
    Button,
    IconButton,
    Paper,
    Popper,
    Slider,
    TextField,
    Typography,
} from '@material-ui/core'
import { Close, ExpandMore } from '@material-ui/icons'
import { useTranslation } from 'react-i18next'

import * as actions from '../../store/actions'
import { connect } from 'react-redux'
import Upload from 'antd/es/upload'
import { uploadFile } from '../../utils/uploadFile'
import { ChromePicker } from 'react-color'
import ImageGallery from './ImageGallery'

const PickerPopper = props => {
    const { t } = useTranslation()

    const {
        el,

        setEditorKey,
        editorKey,
        saveValueMM,
        templateMasterBoxId,
        templateMasterVar,
    } = props
    const [imageGalleryOpen, setImageGalleryOpen] = useState(false)
    const id = templateMasterBoxId || props.id
    const [videoURL, setVideoURL] = useState(el.videoURL)

    const keyToText = (editorKey: String) =>
        editorKey === 'imageValue'
            ? t('g.chooseImage')
            : editorKey === 'videoUrl'
            ? t('g.setMedia')
            : editorKey === 'styleBlock'
            ? t('g.styleBlock')
            : editorKey === 'styleLine'
            ? t('g.styleLine')
            : editorKey === 'lineType'
            ? t('g.lineType')
            : editorKey === 'lineWidth'
            ? t('g.lineWidth')
            : editorKey === 'lineColor'
            ? t('g.lineColor')
            : editorKey === 'bgColor'
            ? t('g.bgColor')
            : editorKey === 'radius'
            ? t('g.radius')
            : ''

    return (
        <>
            <Popper
                open={Boolean(editorKey)}
                anchorEl={() => document.getElementById('anchorTR')}
                placement="bottom-end"
                // anchorOrigin={{
                //     vertical: 'bottom',
                //     horizontal: 'left',
                // }}
                // transformOrigin={{
                //     vertical: 'top',
                //     horizontal: 'right',
                // }}
            >
                <Paper style={{ maxWidth: '250px', padding: '10px' }}>
                    <div onMouseDown={e => e.stopPropagation()}>
                        <div style={{ display: 'flex' }}>
                            <div
                                style={{
                                    flex: 1,
                                    display: 'flex',
                                    alignItems: 'center',
                                }}
                            >
                                <Typography>{keyToText(editorKey)}</Typography>
                            </div>
                            {!templateMasterBoxId && (
                                <IconButton
                                    color="primary"
                                    size="small"
                                    onClick={() =>
                                        setEditorKey(
                                            [
                                                'lineWidth',
                                                'lineType',
                                                'lineColor',
                                            ].includes(editorKey)
                                                ? 'styleLine'
                                                : [
                                                      'radius',
                                                      'bgColor',
                                                  ].includes(editorKey)
                                                ? 'styleBlock'
                                                : ''
                                        )
                                    }
                                >
                                    <Close />
                                </IconButton>
                            )}
                        </div>
                        {['lineColor', 'bgColor'].includes(editorKey) ? (
                            <ChromePicker
                                color={
                                    el[editorKey] || {
                                        r: 255,
                                        g: 255,
                                        b: 255,
                                        a: 1,
                                    }
                                }
                                // width={150}
                                // height={150}
                                onChange={color =>
                                    saveValueMM(id, {
                                        [editorKey]: color.rgb,
                                    })
                                }
                                onChangeComplete={color =>
                                    saveValueMM(id, {
                                        [editorKey]: color.rgb,
                                    })
                                }
                            />
                        ) : ['lineWidth', 'radius'].includes(editorKey) ? (
                            <Slider
                                min={editorKey === 'radius' ? 0 : 1}
                                max={
                                    editorKey === 'radius'
                                        ? 50
                                        : editorKey === 'lineWidth'
                                        ? 20
                                        : 100
                                }
                                step={1}
                                value={el.radius}
                                onChange={(_e, value) =>
                                    saveValueMM(id, { [editorKey]: value })
                                }
                                style={{
                                    width: '200px',
                                }}
                            />
                        ) : editorKey === 'lineType' ? (
                            <div
                                style={{
                                    display: 'flex',
                                    flexDirection: 'column',
                                    alignItems: 'center',
                                }}
                            >
                                {[
                                    'fluid',
                                    'straight',
                                    'arc',
                                    'magnet',
                                    'grid',
                                    'no',
                                ].map(type => (
                                    <div>
                                        <Button
                                            onClick={() =>
                                                saveValueMM(id, {
                                                    lineType: type,
                                                })
                                            }
                                        >
                                            {type}
                                        </Button>
                                    </div>
                                ))}
                            </div>
                        ) : editorKey === 'videoURL' ? (
                            <div>
                                {t('g.addMedia')}

                                <TextField
                                    autoFocus
                                    margin="dense"
                                    label="URL"
                                    type="url"
                                    fullWidth
                                    defaultValue={el.videoURL}
                                    onChange={e => setVideoURL(e.target.value)}
                                />
                                <div>
                                    {!templateMasterBoxId && (
                                        <Button
                                            onClick={() => setEditorKey('')}
                                            color="primary"
                                        >
                                            {t('g.cancel')}
                                        </Button>
                                    )}
                                    <Button
                                        onClick={() => {
                                            saveValueMM(id, { videoURL })
                                            if (!templateMasterBoxId)
                                                setEditorKey('')
                                        }}
                                        color="primary"
                                    >
                                        {t('g.confirm')}
                                    </Button>
                                </div>
                                <Accordion>
                                    <AccordionSummary
                                        expandIcon={<ExpandMore />}
                                        aria-controls="panel1a-content"
                                        id="panel1a-header"
                                    >
                                        <Typography>
                                            Supported video types:
                                        </Typography>
                                    </AccordionSummary>
                                    <AccordionDetails>
                                        <Typography>
                                            {t('g.addMediaDesc')}
                                        </Typography>
                                    </AccordionDetails>
                                </Accordion>
                            </div>
                        ) : editorKey === 'imageValue' ? (
                            <div
                                style={{
                                    display: 'flex',
                                    flexDirection: 'row',
                                    alignItems: 'center',
                                    marginTop: '10px',
                                }}
                            >
                                <Button
                                    variant="contained"
                                    color="primary"
                                    style={{
                                        marginRight: '10px',
                                    }}
                                    onClick={() => setImageGalleryOpen(true)}
                                >
                                    Gallery
                                </Button>
                                <Upload
                                    accept="image/*"
                                    showUploadList={false}
                                    customRequest={({ file }) => {
                                        uploadFile(
                                            file,
                                            800,
                                            undefined,
                                            url => {
                                                if (url) {
                                                    saveValueMM(id, {
                                                        imageValue: url,
                                                    })
                                                }
                                            }
                                        )
                                    }}
                                >
                                    <Button
                                        color="primary"
                                        variant="contained"
                                        style={{
                                            marginRight: '10px',
                                        }}
                                    >
                                        Upload
                                    </Button>
                                </Upload>
                            </div>
                        ) : editorKey === 'styleLine' ? (
                            <div
                                style={{
                                    display: 'flex',
                                    flexDirection: 'row',
                                    alignItems: 'center',
                                    marginTop: '10px',
                                }}
                            >
                                <Button
                                    variant="contained"
                                    color="primary"
                                    style={{
                                        marginRight: '10px',
                                    }}
                                    onClick={() => setEditorKey('lineType')}
                                >
                                    Type
                                </Button>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    style={{
                                        marginRight: '10px',
                                    }}
                                    onClick={() => setEditorKey('lineWidth')}
                                >
                                    Width
                                </Button>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    style={{
                                        marginRight: '10px',
                                    }}
                                    onClick={() => setEditorKey('lineColor')}
                                >
                                    Color
                                </Button>
                            </div>
                        ) : editorKey === 'styleBlock' ? (
                            <div
                                style={{
                                    display: 'flex',
                                    flexDirection: 'row',
                                    alignItems: 'center',
                                    marginTop: '10px',
                                }}
                            >
                                <Button
                                    variant="contained"
                                    color="primary"
                                    style={{
                                        marginRight: '10px',
                                    }}
                                    onClick={() => setEditorKey('bgColor')}
                                >
                                    Background
                                </Button>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    style={{
                                        marginRight: '10px',
                                    }}
                                    onClick={() => setEditorKey('radius')}
                                >
                                    Corners
                                </Button>
                            </div>
                        ) : null}
                    </div>
                </Paper>
            </Popper>
            <ImageGallery
                open={imageGalleryOpen}
                close={() => setImageGalleryOpen(false)}
                onChoose={url => {
                    saveValueMM(id, {
                        [editorKey]: url,
                    })
                }}
            />
        </>
    )
}

const mapStateToProps = state => {
    return {
        templateMasterBoxId: state.templateMasterBoxId,
        templateMasterVar: state.templateMasterVar,
        templateMasterPaused: state.templateMasterPaused,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        saveValueMM: (id, value) => dispatch(actions.saveValueMM(id, value)),
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(PickerPopper)
