import { Button, IconButton } from '@material-ui/core'
import { ArrowBack, ArrowForward, Close, Done } from '@material-ui/icons'
import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'

const TemplateMaster = props => {
    const {
        templateFolderId,
        templateFolder,
        templateStep,
        setTemplateDefault,
        structure,
        setTemplateStepPrev,
        setTemplateStepNext,
        setSelectedBox,
        setTemplateMasterBoxidAndVar,
    } = props

    const parentEl = structure.items[templateFolderId]
    const currentVar = templateFolder.vars[templateStep]

    if (!currentVar || !parentEl) {
        setTemplateDefault()
        return null
    }

    let el = null
    let elId = ''
    if (currentVar.id === 'parent') {
        elId = templateFolderId
        el = parentEl
    } else {
        for (let item of parentEl.children) {
            if (structure.items[item].tempId === currentVar.id) {
                elId = item
                el = structure.items[item]
            }
        }
    }

    if (!el || !elId) {
        setTemplateDefault()
        return null
    }

    useEffect(() => {
        setSelectedBox(elId)
        setTemplateMasterBoxidAndVar(elId, currentVar)
    }, [templateStep])

    return (
        <div style={{ flex: 1, display: 'flex' }}>
            <div
                style={{
                    flex: 1,
                    display: 'flex',
                    flexDirection: 'column',
                }}
            >
                <div>Step {templateStep + 1}</div>
                <div>{currentVar.title}</div>
            </div>
            <div>
                <Button
                    onClick={() => setTemplateStepPrev()}
                    disabled={templateStep <= 0}
                >
                    Back
                </Button>
                <Button
                    onClick={() => setTemplateStepNext()}
                    disabled={templateFolder.vars.length <= templateStep + 1}
                >
                    Next
                </Button>
                <IconButton onClick={() => setTemplateDefault()}>
                    <Close />
                </IconButton>
            </div>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        templateFolderId: state.templateFolderId,
        templateFolder: state.templateFolder,
        templateStep: state.templateStep,
        structure: state.structureMM,
        templateMasterPaused: state.templateMasterPaused,
    }
}
const mapDispatchToProps = (dispatch: any) => {
    return {
        saveStructure: () => dispatch(actions.saveStructure()),
        setTemplateDefault: () => dispatch(actions.setTemplateDefault()),
        setTemplateStepPrev: () => dispatch(actions.setTemplateStepPrev()),
        setTemplateStepNext: () => dispatch(actions.setTemplateStepNext()),
        setTemplateMasterBoxidAndVar: (boxId, varObj) =>
            dispatch(actions.setTemplateMasterBoxidAndVar(boxId, varObj)),
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(TemplateMaster)
